.intro {
  border: 2px solid red;
  margin: 50px 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 20px;
  svg {
    width: 400px;
  }
  @media (max-width: 800px) {
    flex-direction: column;
    margin: unset;
    padding: unset;
    svg {
      width: 200px;
    }
  }
}
